.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} 

@media screen and (max-width:990px) {
  #page-body .latest_post_container div, .welcome_section div {
    width:100%; /* The width is 100%, when the viewport is 990px or smaller */
  }

  #page-body a.redirect_button {
    font-size: 25px;
  }

  .main_logo .dosoutddors_logo_home {
    width:70%; /* The width is 100%, when the viewport is 990px or smaller */
  }
}

@media screen and (min-width:1600px) {
  .main_logo .dosoutddors_logo_home {
    width:30%; /* The width is 30%, when the viewport is 1600px or larger */
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*
Tutorial
*/

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

.App > nav {
  border-bottom: 1px solid #023144;;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  display:block;
  z-index: 2;
  opacity: 0.75;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  float: left;
}

nav a {
  text-decoration: none;
  color: #023144;
  font-weight: bold;
  padding: 20px 32px 17px 32px;
}

nav a:hover {
  /* background: black; */
  background: #023144;
  color: white;
}

#page-body {
  /* max-width: 700px; */
  margin: auto;
  padding-top: 64px;
  width: 68%;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.article_main_image_element, img.single_articlkeImage{
  max-width: 90%;
  height: auto;
}

.main_image_meta{
  color: #666;
  font-size: 14px;
  padding-bottom: 15px;
  font-family: 'Open Sans',sans-serif;
  text-align: left;
}

.article_paragraph, .preview_par{
  font-family: 'Open Sans',sans-serif;
  line-height: 2em;
  color: #000000;
  text-align: left;
  /* font-size:1vw; */

  padding-bottom: 1em;
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.home_main_image{
  width: 100%;
  background-repeat: no-repeat;
  background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/en_cover_pic.png);
  /* background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/clearwater.png); */
  /* background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/IMG-9582.jpg); */
  box-shadow: 0 0 20px rgb(0 0 0 / 20%);
  background-attachment: scroll;
  background-position: center bottom;
  border: none;
  float: left;
  height: 500px;
  background-size: cover;
  position: static;
}

.home_image_container{
  /* padding-top: 58px; */
  position: relative;
}

.main_logo_conatiner {
  padding: 30px 20px 0 20px;
  height: 94%;
  transition: opacity .6s ease-in;
  /* display: table; */
}

.main_logo {
  width: 100%;
  border: none;
  margin: auto;
  height: 89%;
  position: absolute;
}

.main_logo span{
  font-family: Charmonman;
  font-size: 50px;
  font-weight: bold;
  /* color: darksalmon; */
}

.App nav ul a {
  opacity: 1;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: bolder;
  text-transform: uppercase;
}

.hand_written_header{
  font-family: Reenie Beanie;
  font-size: 50px;
  color: #023144;
}

.social_wrapper {
  width: auto;
  margin: 0;
  left: 30px;
  text-align: left;
  position: absolute;
}



.social_block {
  height: 100%;
  padding: 10px 4px 0 4px;
  float: right;
}
.social_block a {
  padding: 8px;
}

.pinterestLogo{
  height: 28px;
  width:28px;
}

