.latest_post_container, .welcome_section {
  width: 100%;  
  position: relative;
  display: inline-block;
}

.latest_post_container > div{
  /* display: block; */
  float:left;
  width:31%;
  margin: 1%;
  padding-top:100%;
  padding: 10px;
  box-shadow: 0 0 10px rgb(0 0 0 / 20%);
  /* height: 100%; */
}

.latest_post_container > div:hover{
  box-shadow: 0 0 30px rgb(0 0 0 / 20%);
}

.aspect_container{
  width: 100%;
  padding-top: 100%; /* 1:1 Aspect Ratio */
  position: relative;
}

.aspectBox{
  position: absolute;
  background-color: #333033;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  color: white;
  font-weight: bold;
}

* {
  box-sizing:border-box;
}

/* Logo */
img.dosoutddors_logo_home {
  width: 45%;
  opacity: 1;
}

.image_filter {
  opacity: 0.05;
  background-color: #a9a9a9;
  height: 500px;
}
.aspectBox img{
  width: 100%;

}

footer.dos_outdoors_footer {
  height: 200px;
  width: 100%;
  background-color: #023144;
  color: white;
  padding: 15px 20px;
  margin-top: 50px;
  box-shadow: 0 0 20px rgb(0 0 0 / 20%);
}

.intro_section {
  display: inline-block;
  margin-top: 15px;
}

a.redirect_button {
  background-color: #15495e;
  color: white;
  padding: 15px 50px;
  border-radius: 9px;
  font-family: 'Open Sans',sans-serif;
  font-weight: bold;
  font-size: 17px;
  text-decoration: none;
  box-shadow: 0 0 20px rgb(0 0 0 / 20%);

}

a.redirect_button:hover{
  background-color: white;
  color: #15495e;
}

.more_art_btn_cont {
  width: 100%;
  margin: 40px 0 11px 0;
  height: 30px;
}

.welcome_left{
  float: left;
  width:45%;  
  padding: 40px 25px 30px 25px;
}

.welcome_right{
  float: right;
  width:55%; 
}

.welcome_left .aspectBox{
  overflow: hidden;
  border-radius: 10000px;
  background-color: white;
}

.article_paragraph.responsive_reading_font{
  font-size: 1.7vw;
}

.footer_left_section {
  float: left;
  width: 50%;
}
.footer_right_section {
  float: right;
  width: 50%;
}
footer p, footer a{
  margin: 10px 10px 10px 0;
  text-decoration: none;
  color:  white;
  width: 100%;
}